.grid {
    display: flex;
    flex-wrap: wrap;
    margin: -20px;

    &__item {
        flex: 0 0 350px;
        // min-width: 250px
        padding: 20px; } }

.card {
    position: relative;
    cursor: pointer;
    transition: all .2s ease-in-out;
    height: 100%;
    &:hover {
        box-shadow: 0 8px 8px rgba(0, 0, 0, 0.4);
        transform: translateY(-8px); } }

