.form {
    &__part {
        &:not(:last-child) {
            margin: 0 0 24px; } }

    &__row {
        display: flex;
        margin: 0 -4px;
        &:not(:last-child) {
            margin: 0 0 8px; } }

    &__label {
        flex: 1;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding: 0 5px;

        &-title {
            flex: 1 0 100%;
            margin-bottom: 8px; } }

    &__input {
        flex: 1 0 100%;
        height: 30px;
        outline: none;
        border: none;
        padding: 0 8px; }

    &__textarea {
        width: 100%;
        height: 80px;
        resize: none;
        border: none;
        outline: none;
        padding: 8px; }

    &__footer {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px; } }
