.modal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.5);
    z-index: 1000;
    padding: 100px 0;

    &__inner {
        max-width: 900px;
        margin: 0 auto;
        background: $details-color;
        padding: 16px;
        border-radius: 16px; }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 16px; }

    &__title {
        flex: 1; }

    &__close {
        width: 20px;
        height: 20px;
        background: red;
        cursor: pointer; } }
